// use-register-actions v1: Hook
//  RegisterPage.js 

import { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';

import {
  sendOTP,
  verifyOTP,
  createUserWithPassword,
} from '../../../services/supabase/auth';
import { getErrorMessage, AUTH_ERRORS } from '../../../utils/authErrorHandler';

/**
 * useRegisterActions Hook - 
 * @param {Object} params - 
 * @param {Object} params.formData - 
 * @param {Function} params.validateForm - 
 * @param {Function} params.setLoading - 
 * @param {Function} params.setSendingCode - 
 * @param {Function} params.setError - 
 * @param {Function} params.setGeneralError - 
 * @returns {Object} 
 */
const useRegisterActions = ({
  formData,
  validateForm,
  setLoading,
  setSendingCode,
  setError,
  setGeneralError,
  onStepChange, // 
}) => {
  const navigate = useNavigate();

  //  OTP 
  const sendVerificationCode = useCallback(
    async email => {
      if (!email) {
        setError('email', AUTH_ERRORS.EMPTY_FIELD);
        return;
      }

      setSendingCode(true);
      try {
        //  Supabase OTP 
        const { success, error } = await sendOTP(email);

        if (success) {
          setSendingCode(false);
          // 
          setError(
            'email',
            ' Verification code sent! Please check your inbox.'
          );
          //  OTP 
          if (onStepChange) {
            onStepChange('otp');
          }
        } else {
          setSendingCode(false);
          setError('email', getErrorMessage({ message: error }));
        }
      } catch (error) {
        console.error('Failed to send verification code:', error);
        setSendingCode(false);
        setError('email', 'Failed to send verification code, please try again');
      }
    },
    [setSendingCode, setError]
  );

  //  -  OTP 
  const handleSubmit = useCallback(
    async e => {
      e.preventDefault();

      if (!validateForm()) {
        return;
      }

      setLoading(true);
      try {
        //  Edge Function 
        const {
          success: createSuccess,
          user,
          session,
          error: createError,
        } = await createUserWithPassword(
          formData.email,
          formData.verificationCode,
          formData.password
        );

        if (!createSuccess) {
          setLoading(false);
          setGeneralError(getErrorMessage({ message: createError }));
          return;
        }

        // 
        console.log('Registration successful:', user);
        setLoading(false);
        alert('Registration successful! You are now logged in.');

        // 
        if (user?.id) {
          navigate(`/artist/${user.id}`);
        } else {
          navigate('/');
        }
      } catch (error) {
        console.error('Registration error:', error);
        setLoading(false);
        setGeneralError('Registration failed, please try again');
      }
    },
    [formData, validateForm, setLoading, setGeneralError, navigate]
  );

  // 
  const handleLoginClick = useCallback(() => {
    navigate('/login');
  }, [navigate]);

  return {
    sendVerificationCode,
    handleSubmit,
    handleLoginClick,
  };
};

export default useRegisterActions;
