import { createClient } from '@supabase/supabase-js';

// Supabase CRA  Vite
const supabaseUrl =
  process.env.REACT_APP_SUPABASE_URL || import.meta?.env?.VITE_SUPABASE_URL;
const supabaseKey =
  process.env.REACT_APP_SUPABASE_ANON_KEY ||
  import.meta?.env?.VITE_SUPABASE_ANON_KEY;

// 
if (!supabaseUrl || !supabaseKey) {
  console.warn(' Supabasemock');
  console.warn(
    ' REACT_APP_SUPABASE_URL  REACT_APP_SUPABASE_ANON_KEY (CRA)  VITE_SUPABASE_URL  VITE_SUPABASE_ANON_KEY (Vite)'
  );
}

// Supabase
export const supabase = createClient(
  supabaseUrl || 'https://placeholder.supabase.co',
  supabaseKey || 'placeholder-key'
);

// 
export const checkSupabaseConnection = async () => {
  // 
  if (!supabaseUrl || !supabaseKey) {
    console.log(' Supabasemock');
    return false;
  }

  try {
    // 
    // 
    const {
      data: { session },
      error,
    } = await supabase.auth.getSession();

    if (error) {
      console.warn(' Supabasemock:', error.message);
      return false;
    }

    console.log(' Supabase');
    console.log(' :', session ? '' : '');
    return true;
  } catch (error) {
    console.warn(' Supabasemock:', error.message);
    return false;
  }
};

// 
export default supabase;
