import React, { useState, useEffect } from 'react';

import {
  supabase,
  checkSupabaseConnection,
} from '../services/supabase/client.js';

const SupabaseConnectionTest = () => {
  const [connectionStatus, setConnectionStatus] = useState('checking');
  const [envVars, setEnvVars] = useState({});
  const [testResults, setTestResults] = useState({});

  useEffect(() => {
    // 
    setEnvVars({
      VITE_SUPABASE_URL:
        process.env.REACT_APP_SUPABASE_URL ||
        import.meta?.env?.VITE_SUPABASE_URL,
      VITE_SUPABASE_ANON_KEY:
        process.env.REACT_APP_SUPABASE_ANON_KEY ||
        import.meta?.env?.VITE_SUPABASE_ANON_KEY
          ? ' '
          : ' ',
    });

    // 
    const testConnection = async () => {
      try {
        const isConnected = await checkSupabaseConnection();
        setConnectionStatus(isConnected ? 'connected' : 'disconnected');

        // 
        await testBasicFunctions();
      } catch (error) {
        console.error('Connection test failed:', error);
        setConnectionStatus('error');
      }
    };

    testConnection();
  }, []);

  const testBasicFunctions = async () => {
    const results = {};

    try {
      //  auth 
      results.auth = !!supabase.auth;

      //  from 
      results.from = !!supabase.from;

      //  storage 
      results.storage = !!supabase.storage;

      setTestResults(results);
    } catch (error) {
      console.error('Basic functions test failed:', error);
    }
  };

  const getStatusColor = status => {
    switch (status) {
      case 'connected':
        return 'text-green-600 bg-green-100';
      case 'disconnected':
        return 'text-yellow-600 bg-yellow-100';
      case 'error':
        return 'text-red-600 bg-red-100';
      default:
        return 'text-gray-600 bg-gray-100';
    }
  };

  const getStatusText = status => {
    switch (status) {
      case 'connected':
        return ' ';
      case 'disconnected':
        return ' ';
      case 'error':
        return ' ';
      default:
        return ' ...';
    }
  };

  return (
    <div className='max-w-2xl mx-auto p-6 bg-white rounded-lg shadow-lg'>
      <h1 className='text-2xl font-bold mb-6 text-center'>
        Supabase 
      </h1>

      {/*  */}
      <div className='mb-6 p-4 border rounded-lg'>
        <h2 className='text-lg font-semibold mb-3'> </h2>
        <div className='space-y-2'>
          <div>
            <strong>REACT_APP_SUPABASE_URL:</strong>
            <div className='text-gray-600 break-all'>
              {envVars.REACT_APP_SUPABASE_URL || ''}
            </div>
          </div>
          <div>
            <strong>REACT_APP_SUPABASE_ANON_KEY:</strong>
            <div className='text-gray-600'>
              {envVars.REACT_APP_SUPABASE_ANON_KEY}
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <div className='mb-6 p-4 border rounded-lg'>
        <h2 className='text-lg font-semibold mb-3'> </h2>
        <div className='flex items-center space-x-2'>
          <span>:</span>
          <span
            className={`px-3 py-1 rounded-full text-sm font-medium ${getStatusColor(
              connectionStatus
            )}`}
          >
            {getStatusText(connectionStatus)}
          </span>
        </div>
      </div>

      {/*  */}
      <div className='mb-6 p-4 border rounded-lg'>
        <h2 className='text-lg font-semibold mb-3'> </h2>
        <div className='space-y-2'>
          <div>
            <strong>Auth :</strong>
            <span
              className={`ml-2 px-2 py-1 rounded text-sm ${
                testResults.auth
                  ? 'bg-green-100 text-green-700'
                  : 'bg-red-100 text-red-700'
              }`}
            >
              {testResults.auth ? ' ' : ' '}
            </span>
          </div>
          <div>
            <strong>Database :</strong>
            <span
              className={`ml-2 px-2 py-1 rounded text-sm ${
                testResults.from
                  ? 'bg-green-100 text-green-700'
                  : 'bg-red-100 text-red-700'
              }`}
            >
              {testResults.from ? ' ' : ' '}
            </span>
          </div>
          <div>
            <strong>Storage :</strong>
            <span
              className={`ml-2 px-2 py-1 rounded text-sm ${
                testResults.storage
                  ? 'bg-green-100 text-green-700'
                  : 'bg-red-100 text-red-700'
              }`}
            >
              {testResults.storage ? ' ' : ' '}
            </span>
          </div>
        </div>
      </div>

      {/*  */}
      <div className='text-center'>
        <button
          onClick={() => window.location.reload()}
          className='px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors'
        >
          
        </button>
      </div>

      {/*  */}
      <div className='mt-6 p-4 bg-gray-50 rounded-lg'>
        <h3 className='font-semibold mb-2'> </h3>
        <ul className='text-sm text-gray-600 space-y-1'>
          <li>  .env.local </li>
          <li>  Supabase </li>
          <li> </li>
          <li> : /test-supabase</li>
        </ul>
      </div>
    </div>
  );
};

export default SupabaseConnectionTest;
